.hall-plan{--seat-height: 40px;--gutter: 10px;--border-width: 2px;--seat-bg: #afe34a;--border-color: #8bc21e;font-weight:500;color:#363a06}.hall-plan__rows{overflow:hidden}.hall-plan__row{display:flex}.hall-plan__row:not(:last-child){margin-bottom:10px;margin-bottom:var(--gutter)}.hall-plan__row-number{width:5%;max-width:50px;height:40px;height:var(--seat-height);line-height:40px;line-height:var(--seat-height);text-align:center;font-size:20px}.hall-plan__row-number:first-child{margin-right:10px;margin-right:var(--gutter)}.hall-plan__row-number:last-child{margin-left:10px;margin-left:var(--gutter)}.hall-plan__row-content{flex:1 0 auto}.hall-plan--ltr .hall-plan__row-content{margin-left:-10px;margin-left:-var(--gutter)}.hall-plan--rtl .hall-plan__row-content{margin-right:-10px;margin-right:-var(--gutter)}.hall-plan__row-content::after{content:'';display:table;width:0;height:0;clear:both}.hall-plan__seat{height:40px;height:var(--seat-height);line-height:36px;line-height:calc(var(--seat-height) - var(--border-width) * 2);border-radius:8px;text-align:center;background-color:#afe34a;background-color:var(--seat-bg);font-size:20px;user-select:none;transition:background-color 200ms ease;border:2px solid #afe34a;border-width:var(--border-width);border-color:var(--seat-bg)}.hall-plan__seat--active{border-color:#8bc21e;border-color:var(--border-color);background-color:#c3ea76}.hall-plan--clickable .hall-plan__seat{cursor:pointer}.hall-plan--clickable .hall-plan__seat:hover{background-color:#c3ea76}.hall-plan__spacer{height:40px;height:var(--seat-height)}.hall-plan--ltr .hall-plan__spacer{float:left;padding-left:10px;padding-left:var(--gutter)}.hall-plan--rtl .hall-plan__spacer{float:right;padding-right:10px;padding-right:var(--gutter)}.hall-plan__screen-wrap{display:flex}.hall-plan__screen-spacer{flex:0 0 auto;width:5%;max-width:50px}.hall-plan__screen-spacer:first-child{margin-right:10px;margin-right:var(--gutter)}.hall-plan__screen-spacer:last-child{margin-left:10px;margin-left:var(--gutter)}.hall-plan__screen{flex:1 0 auto;position:relative;margin-bottom:10px;margin-bottom:var(--gutter)}.hall-plan__screen-text{position:absolute;bottom:20%;left:50%;transform:translateX(-50%);text-transform:uppercase;color:#888;font-size:0.8em}
